import React, { useEffect } from 'react'
import {Link} from 'react-router-dom'
import './css/header.css'

function Header() {
    useEffect(()=>{
        function Scroll() {
            const nav = document.querySelector("#nav")
            const topButton = document.querySelector("#top")
            //       scrollon css  
            if(window.scrollY>=200) {
                nav.classList.add("scrollon")
            }
            else {
                nav.classList.remove("scrollon")
            }
            if(window.scrollY>=700) {
                // 1500   top  
                topButton.classList.add("scrollon")
            }
            else{
                topButton.classList.remove("scrollon")
            }
        }
        window.addEventListener("scroll",Scroll)
        return ()=> {
            window.removeEventListener("scroll",Scroll)
        }
    },[])
    return (
        <div>
            <header className="head">
        <nav className="nav" id="nav">
                <div className='img'></div>
            <div className="logo">
                <span className='nav-title'>Busan Bada Lure</span>
                </div>
            <ul className="nav-menu">
                <span id='color1'><Link to = "/"></Link> &nbsp;</span>
                <span id='color2' className='space'><Link to = "/weather"> / </Link> &nbsp;</span>
                <span id='color3'><Link to = "/point"> </Link></span>
                <span id='color4'><Link to = "/post"></Link></span>
                <span id='color5'><Link to = "/login">Login</Link></span>
            </ul>
        </nav>

    </header>
        </div>
    )
}

export default Header