import React, { useState } from 'react'
import './css/post.css'
import PostModalBasic from './postModalBasic';

export default function PostList(props) {
  //  item id, title, date, modalContent 
  const {items} = props

  //   ,     state
  const [modalOpen,setModalOpen] = useState(false)
  const [selectedPost, setSelectedPost] = useState();

  //  
  // item modalContent   selectedPost state  
  // modalOpen state  true    
  const showModal = (content) => {
    setSelectedPost(content)
    setModalOpen(true);
  }
  return (
    <div>
      {items.map((item) => (
        <div className='post-list' key={item.id} onClick={() => showModal(item.modalContent)}>
          <div className='title'>{item.title}</div>
          <div>{item.date}</div>
        </div>
      ))}

      {/* 
          
        setModalOpen  modalOpen state 
        content   item modalContent 
      */}
      {
        modalOpen && 
        <PostModalBasic setModalOpen={setModalOpen} content={selectedPost}/>
      }
    </div>
  );
}
