import React, { useState } from 'react';
import "./css/time.css"


const calenders = [
    { month: '1', img: 'https://ifh.cc/g/9b0AGy.png' },
    { month: '2', img: 'https://ifh.cc/g/XfWh2L.png' },
    { month: '3', img: 'https://ifh.cc/g/jBjkDs.png' },
    { month: '4', img: 'https://ifh.cc/g/nFq8Ny.png' },
    { month: '5', img: 'https://ifh.cc/g/B1kBL0.png' },
    { month: '6', img: 'https://ifh.cc/g/B9ft8m.png' },
    { month: '7', img: 'https://ifh.cc/g/FQkbyM.png' },
    { month: '8', img: 'https://ifh.cc/g/BgLLyc.png' },
    { month: '9', img: 'https://ifh.cc/g/FcvYnT.png' },
    { month: '10', img: 'https://ifh.cc/g/s9MsBO.png' },
    { month: '11', img: 'https://ifh.cc/g/l3gm5r.png' },
    { month: '12', img: 'https://ifh.cc/g/fdLB1N.png' },
]

//        
const thisMonth = new Date().getMonth();

//     ,  onClick
function MonthButton({month,onClick}) {
    return (
        <button className='time-btn' onClick={onClick}>
            {month}
        </button>
    )
}


export default function Time() {
    const [selectMonth, setSelectMonth] = useState(thisMonth+1+"");
    //     selectMonth 
    function monthButtonClick(month) {
        setSelectMonth(month);
    }

    return(
        <div id='time'>
            <div id='time-title'>
                <h1>Calender</h1>
            </div>
            <div id='buttons'>
                <h3>2023</h3>
                {
                    // calender   MonthButton 
                    //   monthButtonClick , select 
                    calenders.map(({month})=>(
                        <MonthButton
                            key={month}
                            month={month}
                            onClick={()=>monthButtonClick(month)}
                        />
                    ))
                }
            </div>
            {   
                // selectMonth    
                selectMonth &&
                    <div id='image'>
                        <img 
                            src={calenders.find(({month}) => month === selectMonth).img} 
                            alt={`${selectMonth}`} 
                            />
                    </div>
            }
            
        </div>
    )
}
