import React from 'react'
import PostList from './postList'
import './css/post.css'




export default function Post() {
    const list = [
        {id:1, title : " ()     ", date : "(2023-05-01)", modalContent : "2023 05 01      ."},
        {id:2, title : "# ()   ", date : "(2023-05-02)", modalContent : "   (5/5)             ."},
        {id:3, title : "# ()     ", date : "(2023-05-03)", modalContent : "BusanBadaLure            --"},
        {id:4, title : "# (Tip)     ", date : "(2023-05-04)", modalContent : "               ."},
        {id:5, title : "# (Tip)     ", date : "(2023-05-05)", modalContent : "              ."},
    ]


  return (
    <div className='post'>
        <h1>Announcement</h1>
        <div className='post-list'>
        <PostList items={list}/>
        </div>
    </div>
  )
}
